import {distributeCount, getRandomInt, getWeightedRandomElement, shuffle} from "../misc/Utils";

export const EXPLORATION_CANDIDATE_PRIORITY_MUST = 0;
export const EXPLORATION_CANDIDATE_PRIORITY_FILL = 1;

export class ZoneCreator {
    constructor(universe) {
        this.universe = universe;
        this.explorationCandidates = {};
    }

    addExplorationCandidate({planet, exploration, count = 1, priority = EXPLORATION_CANDIDATE_PRIORITY_FILL}) {
        if (!(planet.id in this.explorationCandidates)) {
            this.explorationCandidates[planet.id] = [];
            this.explorationCandidates[planet.id][EXPLORATION_CANDIDATE_PRIORITY_MUST] = [];
            this.explorationCandidates[planet.id][EXPLORATION_CANDIDATE_PRIORITY_FILL] = [];
        }

        this.explorationCandidates[planet.id][priority].push({count, exploration});
    }

    getCandidates(planet, priority) {
        if (planet.id in this.explorationCandidates) {
            return this.explorationCandidates[planet.id][priority];
        }

        return [];
    }

    addExplorationCandidatesWithDistribution({countFrequencies, explorationCreator, minTotal = 0, maxTotal, ...params}) {
        let targetResourcesNum = getRandomInt(minTotal, maxTotal);
        let counts = distributeCount(targetResourcesNum, countFrequencies);

        for (let [stashSize, numOfStashes] of Object.entries(counts)) {
            let exploration = explorationCreator(stashSize.count);
            this.addExplorationCandidates({exploration, minTotal: numOfStashes, maxTotal: numOfStashes, ...params});
        }
    }

    planetsDistributionAccordingToFreeSpace(filter) {
        let planets = this.universe.planets;

        if (filter) {
            planets = planets.filter(filter);
        }

        return planets.map(planet => {
            return {
                planet,
                weight:
                    Math.max(
                        0.1,
                        planet.targetSize - (this.getCandidates(planet, EXPLORATION_CANDIDATE_PRIORITY_MUST).length + this.getCandidates(planet, EXPLORATION_CANDIDATE_PRIORITY_FILL).length / 2.0)
                    )
            }
        });
    }

    addExplorationCandidates({exploration, minPerPlanet = 0, minPerPlanetPerSize = 0, maxPerPlanet = null, maxPerPlanetPerSize = null, minTotal = 0, maxTotal = null, filter}) {
        let numExplorations = 0;
        let planetCounts = {};
        let planetSurfaceMins = {};
        let planetSurfaceMaxs = {};

        let stage = (planetListGenerator) => {
            while (true) {
                if (maxTotal !== null && numExplorations >= maxTotal) {
                    break;
                }

                const planets = planetListGenerator();

                if (planets.length === 0) {
                    break;
                }

                const planet = getWeightedRandomElement(planets).planet;

                this.addExplorationCandidate({
                    planet,
                    exploration,
                    priority:
                        numExplorations < minTotal || planetCounts[planet.id] < planetSurfaceMins[planet.id]
                            ? EXPLORATION_CANDIDATE_PRIORITY_MUST : EXPLORATION_CANDIDATE_PRIORITY_FILL
                });

                numExplorations += 1;
                planetCounts[planet.id] += 1;
            }
        };

        //
        // 1. Set planet counts, mins and maxes
        //
        for (let planet of this.universe.planets.filter(filter)) {
            planetCounts[planet.id] = 0;
            planetSurfaceMins[planet.id] = Math.max(planet.targetSize * minPerPlanetPerSize, minPerPlanet);
            planetSurfaceMaxs[planet.id] = null;

            if (maxPerPlanetPerSize !== null) {
                const maxAdjustedPerPlanet = planet.targetSize * maxPerPlanetPerSize;
                if (maxPerPlanet !== null) {
                    planetSurfaceMaxs[planet.id] = Math.min(maxAdjustedPerPlanet, maxPerPlanet);
                } else {
                    planetSurfaceMaxs[planet.id] = maxAdjustedPerPlanet;
                }
            } else if (maxPerPlanet !== null) {
                planetSurfaceMaxs[planet.id] = maxPerPlanet;
            }
        }


        //
        // 2. Make sure we have a minimums reached on all planets
        //
        stage(() => this.planetsDistributionAccordingToFreeSpace(planet =>
            filter(planet) &&
            (planetCounts[planet.id] < planetSurfaceMins[planet.id])
        ));

        //
        // 3. Make sure we have maximums filled per planet
        //
        stage(() => this.planetsDistributionAccordingToFreeSpace(planet =>
            filter(planet) &&
            planetSurfaceMaxs[planet.id] !== null &&
            planetCounts[planet.id] < planetSurfaceMaxs[planet.id]
        ));

        //
        // 4. If we have a total maximum, fill it.
        //
        if (maxTotal !== null) {
            stage(() => this.planetsDistributionAccordingToFreeSpace(planet =>
                filter(planet)
            ));
        }

        if (numExplorations < minTotal) {
            console.error("Unable to reach minTotal", numExplorations, {exploration, minPerPlanet, maxPerPlanet, minPerPlanetPerSize, maxPerPlanetPerSize, minTotal, maxTotal, filter});
        }

        if (maxTotal !== null && numExplorations > maxTotal) {
            console.info("More than max total", numExplorations, maxTotal);
        }
    }

    fillUniverse() {
        for (let planet of this.universe.planets) {
            this.fillPlanetaryExplorations(planet);
        }

        console.log("Creator finished", this);
    }

    fillPlanetaryExplorations(planet) {
        let explorations = [];

        const addCandidate = ({exploration, count = 1}) => {
            for (let i = 0; i < count; i++) {
                explorations.push(exploration);
            }
        };

        for (let zoneCandidate of this.getCandidates(planet, EXPLORATION_CANDIDATE_PRIORITY_MUST)) {
            addCandidate(zoneCandidate);
        }

        const fillCandidates = this.getCandidates(planet, EXPLORATION_CANDIDATE_PRIORITY_FILL).slice();
        shuffle(fillCandidates);
        for (let i = explorations.length; i < planet.targetSize; i++) {
            if (fillCandidates.length === 0) {
                break;
            }
            addCandidate(fillCandidates.pop());
        }

        shuffle(explorations);
        planet.initExplorations({explorations});
    }
}