import {AlienShip} from "../exploration/demoUniverse";
import {Universe} from "./Universe";
import {shuffle, speak} from "../misc/Utils";
import App from "../components/App";
import {returnToScanner} from "../components/Dialog";
import React from "react";
import Exploration from "./Exploration";
import "./AwayMission.css";

class LandingExploration extends Exploration {
    calculate(mission) {
        let planet = mission.planet;
        return {
            result: <React.Fragment>
                <p>You are landing on {planet.name}, it's a {planet.size} {planet.type} planet.</p>
                <p>{planet.renderImage()}</p>
                <p>Select your team and equipment for this exploration mission.</p>
            </React.Fragment>,
            title: `Landing`,
            voice: `Prepare for landing`
        }
    }
}

