export class Hostilities extends Exploration {
    constructor({title = "Hostilities", combatants = "bugs", combatPower = 1, ...config}) {
        super(config);

        this.title = title;
        this.combatants = combatants;
        this.combatPower = combatPower;
    }

    calculate(mission) {
        return {
            title: this.title,
            result: <React.Fragment>
                <p>Your team was attacked by {this.combatants}, they have {this.combatPower} combat power. Perform a combat round with your away team.</p>
            </React.Fragment>,
            voice: `Danger, we are being attacked!`
        }
    }
}

class AlienShipArrived extends Exploration {
    constructor(ship) {
        super();

        this.ship = ship;
    }

    calculate(mission) {
        let fromLocation = this.ship.location;
        fromLocation.removeBody(this.ship);

        let planet = mission.planet;
        let toLocation = planet.location;
        toLocation.addBody(this.ship);

        mission.possibleExplorations.push(new Hostilities({name: "Hostilities", combatants: "aliens", combatPower: 3}));
        mission.possibleExplorations.push(new Hostilities({name: "Hostilities", combatants: "aliens", combatPower: 3}));
        mission.possibleExplorations.push(new Hostilities({name: "Hostilities", combatants: "aliens", combatPower: 3}));
        shuffle(mission.possibleExplorations);

        return {
            title: `Ship in orbit!`,
            result: <div key="intro">
                <p>An alien ship from the {fromLocation.name} system, warped here (Move the token). It looks like it's sending some kind of pods towards the surface.</p>
            </div>,
            voice: `Alien ship arrived`
        }
    }
}

export class AwayMission {
    constructor(planet) {
        this.planet = planet;
        this.possibleExplorations = [];
        this.firstExploration = true;

        for (let exploration of planet.explorations) {
            this.possibleExplorations.push(exploration);
        }

        for (let danger of planet.dangers) {
            this.possibleExplorations.push(danger);
        }

        for (let item of this.planet.location.bodies) {
            if (item instanceof AlienShip) {
                for (let i = 0; i < 3; i++) {
                    this.possibleExplorations.push(new Hostilities({name: "Hostilities", combatants: "aliens", combatPower: 3}));
                }
            }
        }

        for (let neighbouringLocation of this.planet.location.neighbours(Universe.theUniverse)) {
            for (let item of neighbouringLocation.bodies) {
                if (item instanceof AlienShip) {
                    this.possibleExplorations.push(new AlienShipArrived(item));
                }
            }
        }

        shuffle(this.possibleExplorations);
        this.possibleExplorations.unshift(new LandingExploration()); //add first
        console.log(this.possibleExplorations);
    }

    next() {
        console.assert(this.possibleExplorations.length > 0);
        let exploration = this.possibleExplorations.splice(0, 1)[0];
        let {title, result, voice, buttons} = exploration.onExplorationDialog(this);
        this.planet.markExplored(exploration);

        let isFullyExplored = this.planet.explorations.length === 0;

        let buttonComponents = buttons.map((buttonDefinition, index) =>
            <button key={index} onClick={() => buttonDefinition.onClick()}>
                {buttonDefinition.name}
            </button>
        );

        if (!isFullyExplored) {
            buttonComponents.push(<button key="next" onClick={() => {
                this.next()
            }}>Explore</button>);

            speak(`${voice ? voice : ""}`);
        } else {
            speak(`${voice ? voice : ""}. ${this.planet.name} is now fully explored.`);
        }

        buttonComponents.push(
            <button key="leave" className={!isFullyExplored ? "not-important" : ""} onClick={() => {this.return();}}>
                Leave
            </button>
        );

        App.COMPONENT.setDialog(
            <div className="dialog" key={Math.random()} >
                <div className="dialog-inner">
                    <div className="dialog-content exploration">
                        <h1>{title}</h1>
                        {result}
                    </div>
                    <div className="dialog-bottom">
                        {!isFullyExplored && !this.firstExploration ? <p>This planet still has more to offer. Do you want to explore further?</p>: null}
                        {isFullyExplored ? <p>{this.planet.name} is now fully explored.</p>: null}
                        {buttonComponents}
                    </div>
                </div>
            </div>
        );

        this.firstExploration = false;
    }

    return() {
        returnToScanner();
    }
}