import React from 'react';
import {capitalizeFirst, speak} from "../misc/Utils";
import {Universe} from "../game/Universe";
import {returnToScanner} from "../components/Dialog";
import {SECTORS} from "../data/planetData";
import "./UniverseInfo.css"
import App from "./App"
import {RESOURCES} from "../data/resourceData";

export default class UniverseInfo extends React.Component {
    render() {
        speak("Revealing universe definition ...");

        let universe = Universe.theUniverse;
        console.log(universe.definition);

        let sectors = SECTORS.map((sector) => <div key={sector}>
            <h1>{sector}</h1>
            {
                universe.locations.filter((location) => location.sector === sector).map((location) => <div key={location.id}>
                    <button className="planet-link" key={location.id} onClick={() => App.COMPONENT.selectEntityId(location.id)}>
                        <img src={location.image} alt={location.name} /> {location.name}
                    </button>
                    {
                        location.bodies.length === 0 ? <p>Empty</p> : null
                    }
                    {
                        location.bodies.map((celestialBody => <div key={celestialBody.id}>
                            <p>{celestialBody.renderImage()} {celestialBody.name} ({celestialBody.id})</p>
                            {
                                celestialBody.hasOwnProperty("explorations")
                                    ?
                                    celestialBody.explorations.map((exploration, i) => <div key={i}>
                                        {exploration.onExplorationDialog().result}
                                    </div>)
                                    :
                                    null
                            }
                        </div>))
                    }
                </div>)
            }
        </div>);

        return <div className="dialog">
            <div className="dialog-inner">
                <div className="dialog-content universe-info">
                    <h1>{universe.name} universe</h1>
                    <p>Planets: {universe.planets.length}</p>
                    <p>Other bodies: {universe.celestialBodies.length - universe.planets.length}</p>
                    <p>Locations: {universe.locations.length} ({universe.locations.filter(location => location.bodies.length === 0).length} empty)</p>
                    {
                        RESOURCES.map(resource => {
                            let {provided, required} = Universe.theUniverse.countResource(resource);

                            if (provided || required) {
                                return <p key={resource.id}>{capitalizeFirst(resource.plural)}: {provided} {required ? `( ${required} required)` : null}</p>;
                            }

                            return null;
                        })
                    }
                    <h3>Logs</h3>
                    {Array.from(universe.dataLogs).map(log =>
                        <div style={{float: "left"}} key={log.id}>
                            Log {log.id}:<br/>
                            {log.setIds.map((id) => <span key={id}> {id} </span>)}<br/>
                            <img style={{width: "100px"}} src={log.image} alt={log.id} />
                        </div>
                    )}
                    <h3 style={{clear: "both"}}>Map</h3>
                    {sectors}
                </div>
                <div className="dialog-bottom">
                    <button className="not-important" onClick={() => returnToScanner()}>Back</button>
                </div>
            </div>
        </div>
    }
}
